.header {
  background-color: #000000;
  padding: 25px 22px;
}

.header__form {
  display: flex;
  gap: 13px;
  justify-content: center;
}

.header__input {
  height: 44px;
  min-width: 169px;
  font-family: "Roboto", sans-serif;
  font-size: 22px;
}

.header__button {
  background-color: #393939;
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  font-weight: 400;
  color: #ffffff;
  width: 77px;
  height: 44px;
  border-radius: 0;
  border-style: none;
}
